.product-showcase{padding:40px 0}.product-showcase h1{text-align:center;margin-bottom:40px;color:var(--primary-color);font-size:2.2rem}.product-showcase .category-slider{margin-bottom:60px;position:relative}.product-showcase .category-slider h2{margin-bottom:20px;color:var(--primary-color);font-size:1.6rem;padding-inline:10px}.product-showcase .category-slider .swiper{padding:0 40px}.product-showcase .category-slider .custom-nav{position:absolute;top:50%;transform:translateY(-50%);background-color:#fff;border:2px solid var(--primary-color);color:var(--primary-color);width:40px;height:40px;z-index:10;cursor:pointer;display:flex;align-items:center;justify-content:center;border-radius:6px;font-size:20px;transition:.3s}.product-showcase .category-slider .custom-nav.prev{left:0}.product-showcase .category-slider .custom-nav.next{right:0}.product-showcase .category-slider .custom-nav:hover{background-color:var(--primary-color);color:#fff}.product-showcase .category-slider .custom-nav::after{content:"‹";font-weight:bold}.product-showcase .category-slider .custom-nav.next::after{content:"›"}@media(max-width: 768px){.product-showcase h1{font-size:1.8rem;margin-bottom:30px}.product-showcase .category-slider h2{font-size:1.3rem;text-align:center}.product-showcase .category-slider .swiper{padding:0 10px}.product-showcase .category-slider .custom-nav{width:30px;height:30px;font-size:16px}.product-showcase .category-slider .custom-nav.prev{left:-5px}.product-showcase .category-slider .custom-nav.next{right:-5px}}