.my-orders{padding:40px 20px;max-width:1000px;margin:auto;background-color:#f3f4f6;min-height:100vh}.my-orders h2{text-align:center;color:var(--primary-color);margin-bottom:40px;font-size:28px;font-weight:bold}.my-orders .order-card{background-color:#fff;border-radius:12px;box-shadow:0 4px 12px rgba(0,0,0,.06);margin-bottom:30px;padding:25px 30px;transition:.3s ease}.my-orders .order-card:hover{transform:translateY(-2px);box-shadow:0 6px 15px rgba(0,0,0,.08)}.my-orders .order-card .order-header{display:flex;flex-wrap:wrap;justify-content:space-between;margin-bottom:20px}.my-orders .order-card .order-header span{font-size:16px;color:#34495e;font-weight:500}.my-orders .order-card .order-header .status{padding:6px 14px;border-radius:20px;font-size:14px;font-weight:bold;color:#fff}.my-orders .order-card .order-header .status.قيدالانتظار{background-color:#f39c12}.my-orders .order-card .order-header .status.تمالتأكيد{background-color:#2980b9}.my-orders .order-card .order-header .status.جاريالتوصيل{background-color:#8e44ad}.my-orders .order-card .order-header .status.تمالتوصيل{background-color:#27ae60}.my-orders .order-card .order-header .status.تمالإلغاء{background-color:#c0392b}.my-orders .order-card .order-items{list-style:none;padding:0;margin-bottom:20px}.my-orders .order-card .order-items li{font-size:15px;padding:6px 0;border-bottom:1px solid #eee;color:#555}.my-orders .order-card .order-footer{display:flex;flex-direction:column;gap:6px;font-size:14px;color:#555}.my-orders .order-card .order-footer span{line-height:1.6}.my-orders .empty-orders{text-align:center;color:#888;font-size:18px;padding:60px 0}