.products-page{direction:rtl}.products-page .hero-section{position:relative;background-image:url("../../Assets/الريان.jpg");background-size:cover;background-position:center;height:380px;display:flex;align-items:center;justify-content:center}.products-page .hero-section .overlay{width:100%;height:100%;background:rgba(0,0,0,.55);display:flex;align-items:center;justify-content:center}.products-page .hero-section .overlay .hero-content{color:#fff;text-align:center;padding:0 1rem}.products-page .hero-section .overlay .hero-content h1{font-size:2.5rem;margin-bottom:1rem}.products-page .hero-section .overlay .hero-content p{font-size:1.2rem;color:#f5f5f5}.products-page .hero-section .overlay .hero-content button{margin-top:1.5rem;padding:.8rem 1.5rem;background-color:#f90;border:none;border-radius:5px;font-weight:bold;color:#fff;font-size:1rem;cursor:pointer;transition:.3s ease}.products-page .hero-section .overlay .hero-content button:hover{background-color:#e68a00}.products-page .container{max-width:1200px;margin:3rem auto;padding:3rem 1rem}.products-page .filter-bar{display:flex;justify-content:space-between;align-items:center;margin-bottom:2rem;flex-wrap:wrap;gap:1rem}.products-page .filter-bar label{font-size:1rem;color:#444;display:flex;align-items:center}.products-page .filter-bar label input{margin-left:.5rem;transform:scale(1.2)}.products-page .filter-bar select{padding:.5rem;font-size:1rem;border:1px solid #ccc;border-radius:5px}.products-page .category-section{margin-bottom:3rem}.products-page .category-section .category-title{font-size:2rem;margin-bottom:1rem;border-bottom:2px solid #f90;padding-bottom:.5rem;color:#222;text-align:right}.products-page .category-section .product-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:1.5rem;margin-top:20px;margin-bottom:20px}